  // addrParams={"level":0,"parentId":-1}
  cartParams = {
    "addType": 0,
    "goodsId": 0,
    "cases": 0,
    "piece": 1,
    "levelId": 0,
    "warehouseId": 1
  }
  
  cartGoods: any;
  sumPrice = 0;
 
  // areaList:any;
