  //   this.addrLevelHiddens[this.addrLevelId]=false;
  //   this.addrParams.level = this.addrLevelId;
  //   this.addrParentIds[this.addrLevelId]=item.Id;
  //   this.service.getArea(this.addrParams).then(
  //     (data=>{
  //       this.areaList = data; 
  //       if(data==""){
